/* eslint-disable no-unused-vars */
import React, { Fragment } from 'react';
import {
  Add,
  RefreshOutlined,
  DeleteForeverOutlined,
  SaveOutlined,
} from '@material-ui/icons';
import { getLimit, loadState, saveState, removeState } from 'actions';
import { BtnGroups, Dialog, Image, Slider } from 'components';
import { facilityMaterial, imageMap } from 'data';
import AutoSizer from 'react-virtualized-auto-sizer';
import { FixedSizeGrid } from 'react-window';
import MaterialItem from './MaterialItem';

class Facility extends React.Component {
  state = {
    facility: loadState('facility') || [],
    open: false,
    btns: {
      load: <RefreshOutlined />,
      del: <DeleteForeverOutlined />,
      save: <SaveOutlined />,
      add: <Add />,
    },
  };

  onChange = (value, index) => {
    this.setState(state => ({
      facility: state.facility.map((f, i) => {
        return i === index ? { ...f, value } : f;
      }),
    }));
  };

  onClick = name => () => {
    switch (name) {
      case 'add':
        this.setState({ open: true });
        break;
      case 'load':
        this.setState(state => ({
          facility: loadState('facility') || state.facility,
        }));
        break;
      case 'del':
        removeState('facility');
        break;
      case 'save':
        saveState('facility', this.state.facility);
        break;
      default:
        break;
    }
  };

  handleClose = () => this.setState({ open: false });

  handleCreate = state => {
    const { type } = state;
    const key = type === 'dojo' ? 'weapon' : 'element';
    const field = state[key];
    if (type === 'event' || (type && field)) {
      const max = getLimit(type);
      this.setState(state => ({
        open: false,
        facility: [
          ...state.facility,
          {
            type,
            max,
            field,
            value: [0, max],
          },
        ],
      }));
    }
  };

  removeFacility = index => () => {
    this.setState(state => ({
      facility: state.facility.filter((_, i) => i !== index),
    }));
  };

  sumArray = (list, value) => {
    let sum = 0;
    for (let i = value[0]; i < value[1]; i++) {
      sum += list[i];
    }
    return sum;
  };

  getImage = (item, field) => {
    switch (item) {
      case 'event':
      case 'item':
        return item;
      case 'rupies':
      case 'coin1':
      case 'coin2':
      case 'coin3':
      case 'talonstone':
        return imageMap[item];
      default:
        return imageMap[item][field];
    }
  };

  calcMaterial = facility => {
    const res = {};

    facility.forEach(f => {
      if (f.value[0] !== f.value[1]) {
        const materialDict = facilityMaterial[f.type];
        Object.keys(materialDict).forEach(item => {
          const num = this.sumArray(materialDict[item], f.value);
          if (num !== 0) {
            const key = this.getImage(item, f.field);
            if (res[key]) {
              res[key] += num;
            } else {
              res[key] = num;
            }
          }
        });
      }
    });

    return res;
  };

  render() {
    const { open, btns, facility } = this.state;
    const material = this.calcMaterial(facility);
    const materialArray = Object.keys(material);
    const rowCount = Math.ceil(materialArray.length / 2);
    return (
      <Fragment>
        <div className="column">
          <BtnGroups
            className="col-2 col-4"
            btns={btns}
            onClick={this.onClick}
          />
          <div className="halidom-list">
            {facility.map((f, i) => {
              const image = this.getImage(f.type, f.field);
              return (
                <div
                  key={i}
                  className="gutter-bottom"
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                  }}
                >
                  <Image
                    field="facility"
                    image={image}
                    onClick={this.removeFacility(i)}
                  />
                  <Slider
                    index={i}
                    max={f.max}
                    value={f.value}
                    onChange={this.onChange}
                  />
                </div>
              );
            })}
          </div>
          <Dialog
            upgrade
            open={open}
            type="dojo"
            handleClose={this.handleClose}
            handleCreate={this.handleCreate}
          />
        </div>
        <div className="column">
          <AutoSizer>
            {({ height, width }) => (
              <FixedSizeGrid
                width={width}
                height={height}
                itemData={material}
                columnCount={2}
                columnWidth={width / 2}
                rowCount={rowCount}
                rowHeight={64}
              >
                {MaterialItem}
              </FixedSizeGrid>
            )}
          </AutoSizer>
        </div>
      </Fragment>
    );
  }
}

export default Facility;
