/* eslint-disable no-unused-vars */
import React, { Fragment } from 'react';
import {
  Button,
  Dialog,
  DialogContent,
  DialogActions,
} from '@material-ui/core';
import { translate } from 'actions';
import { elements, facilities, facilityMaterial, imageMap } from 'data';
import Select from './Select';
import withTheme from './withTheme';

class CustomDialog extends React.PureComponent {
  constructor(props) {
    super(props);
    const { type, element, weapon } = props;

    this.state = {
      type,
      element,
      weapon,
    };
  }

  onChange = e => this.setState({ [e.target.name]: e.target.value });

  facilityTemplate = () => {
    const { type } = this.state;
    const { upgrade } = this.props;
    let types = Object.keys(facilityMaterial);
    let label = type === 'dojo' ? 'weapon' : 'element';
    let options = this.props[`${label}s`];
    const disabled = upgrade && type === 'event';

    if (!upgrade) {
      types = types.slice(0, -1);
    } else if (type !== 'event') {
      options = Object.keys(imageMap[type]);
    }

    return (
      <Fragment>
        <DialogContent>
          <Select
            classes="col-2"
            label="type"
            value={type}
            options={types}
            onChange={this.onChange}
          />

          <Select
            classes="col-2"
            disabled={disabled}
            label={label}
            value={this.state[label]}
            options={options}
            onChange={this.onChange}
          />
        </DialogContent>
      </Fragment>
    );
  };

  handleCreate = () => {
    this.props.handleCreate(this.state);
  };

  render() {
    const { lang, open, handleClose } = this.props;
    return (
      <Dialog
        open={open}
        maxWidth="xs"
        classes={{ paperScrollPaper: 'dialog-flex' }}
      >
        {this.facilityTemplate()}
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            {translate('cancel', lang)}
          </Button>
          <Button onClick={this.handleCreate} color="primary">
            {translate('create', lang)}
          </Button>
        </DialogActions>
      </Dialog>
    );
  }
}

CustomDialog.defaultProps = {
  upgrade: false,
  open: false,
  type: 'event',
  element: '',
  weapon: '',
  elements: Object.keys(elements),
  weapons: Object.keys(facilities.weapon),
};

export default withTheme(CustomDialog);
