/* eslint-disable no-unused-vars */
import React, { memo, useContext } from 'react';
import Drawer from '@material-ui/core/Drawer';
import { Context, GitHubIcon, withTheme } from 'components';
import { translate } from 'actions';
import { history } from 'store';

const NavDrawer = memo(({ open, lang, setOpen }) => {
  const { setLang } = useContext(Context);

  const onClick = lang => {
    return () => {
      setLang(lang);
      setOpen(false);
    };
  };

  const toPage = page => {
    return () => history.push(`/${page}/${lang}`);
  };

  return (
    <Drawer
      open={open}
      onClose={() => setOpen(open => !open)}
      classes={{ paper: 'drawer' }}
    >
      <ul>
        <li style={{ position: 'relative' }}>
          <GitHubIcon />
        </li>
        <li onClick={toPage('facility')}>{translate('facility', lang)}</li>
        <li onClick={onClick('en')}>English</li>
        <li onClick={onClick('ja')}>日本語</li>
        <li onClick={onClick('zh')}>简体中文</li>
      </ul>
    </Drawer>
  );
});

export default withTheme(NavDrawer);
